import React, { Component } from 'react';
import styled from 'styled-components';
import Slider from "react-slick";

const SwiperWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  position: absolute;
  background: #0f0f0f;
  display: flex;
  flex-flow: column nowrap;
  color: #fff;
`;

const SwiperHeader = styled.div`
  width: 100%;
  padding: 16px;
  box-sizing: border-box;
`;

const SwiperTitle = styled.h3`
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
  margin: 0;
`;

const SwiperSlick = styled.div`
  width: 100%;
  display: flex;
  flex: 1;
`;

const DishSlide = styled.div`
  width: 100vw;
  height: 100%;
`;

const DishSlideImage = styled.div`
  width: 100vw;
  height: calc(100vw/1.7777);
  display: block;
  background-image: url(${props => props.image});
  background-size: cover;
  background-position: center;
`;

class Swiper extends Component {

  componentWillUnmount() {
    document.body.style.overflowY = 'auto'
  }

  render() {
    if (this.props.isVisible) {
      document.body.style.overflowY = 'hidden'
    } else {
      document.body.style.overflowY = 'auto'
    }

    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };

    return (
      <SwiperWrapper>
        <SwiperHeader>
          <SwiperTitle>
            {this.props.title}
          </SwiperTitle>
        </SwiperHeader>
        <SwiperSlick>
          <Slider {...settings}>
            {this.props.dishes.map((dish) =>
              <DishSlide key={dish.id}>
                <DishSlideImage image={dish.image}/>
                {dish.title}
              </DishSlide>
            )}
          </Slider>
        </SwiperSlick>
      </SwiperWrapper>
    );
  }

}

export default Swiper;
